* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    overscroll-behavior: none;

    font-family: "Ubuntu", sans-serif;

    --md-ref-typeface-plain: 'Ubuntu';
    --md-icon-font: 'Material Symbols Rounded';
    --md-focus-ring-color: transparent;

    -webkit-user-drag: none;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    margin: auto;
    min-height: 100vh;
    max-width: 1200px;

    display: grid;
    grid-template: min-content 1fr / 1fr;

    background-color: var(--md-sys-color-background);
    color: var(--md-sys-color-on-surface);
}

header {
    grid-row: 1 / 2;
    grid-column: 1 / -1;

    display: flex;
    gap: 24px;
    padding: 24px;

    img {
        border-radius: 9px;
    }

    h1 {
        margin: 0;
        font-size: 84px;
        color: var(--md-sys-color-primary);
    }

    div {
        font-size: 48px;
        color: var(--md-sys-color-primary);
    }
}

main {
    height: 100%;
    padding: 48px 24px;

    max-width: 800px;
    justify-self: center;

    display: flex;
    flex-direction: column;
    align-items: center;

    &.posts {
        justify-content: space-between;

        ol {
            padding-top: 18px;
            position: relative;

            &>span {
                position: absolute;
                right: 0;
                top: 0;
                color: var(--md-sys-color-outline-variant);
                font-size: 14px;
            }

            li {
                position: relative;

                &::marker {
                    color: var(--md-sys-color-on-surface-variant);
                    font-size: 24px;
                }

                &::before {
                    content: '';
                    position: absolute;
                    inset: 0;
                    background-color: var(--md-sys-color-primary);
                    border-radius: 12px;
                    z-index: -1;

                    opacity: 0;
                    transition: opacity 0.14s ease-in-out;
                }

                &:hover {
                    &::before {
                        opacity: 0.06;

                    }
                }

                a {
                    display: flex;
                    flex-direction: column;
                    gap: 14px;

                    padding: 24px 12px;

                    h4 {
                        margin: 0;
                        font-size: 32px;

                        transition: font-size 0.24s ease-in-out;
                    }

                    div {
                        color: var(--md-sys-color-on-surface-variant);
                        overflow: hidden;
                        max-width: 600px;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        width: 100vw;
                    }

                    span {
                        color: var(--md-sys-color-outline-variant);
                        font-size: 12px;
                    }

                }
            }
        }
    }

    &.auth {
        justify-content: center;
        grid-row: 1 / 3;

        form {
            display: grid;
            gap: 24px;       
            min-width: 300px;

            span {
                font-size: 48px;
                color: var(--md-sys-color-primary);
            }
        }
    }

    &.new-post {

        form {
            display: grid;
            gap: 24px;      
            min-width: 600px;

            height: 100%;
            grid-template: repeat(2, min-content) 1fr min-content / 1fr;

            span {
                font-size: 48px;
                color: var(--md-sys-color-primary);
            }

            md-outlined-text-field[type="text"] {
                max-width: 400px;
            }

            md-outlined-text-field[type="textarea"] {
                resize: none;
            }

            md-filled-button {
                justify-self: end;
            }
        }
    }

    &.post {
        &>h1 {
            font-size: 48px;
            margin: 12px 0;
            color: var(--md-sys-color-on-surface-variant);
        }

        &>div {
            p {
                font-size: 18px;
            }

            h1,
            h2,
            h3,
            h4,
            h5 {
                margin: 12px 0;
            }
        }

        &>span {
            align-self: end;
            font-size: 14px;
            color: var(--md-sys-color-outline-variant);
        }

        a {
            align-self: start;
        }
    }

    &.post-added {
        grid-row: 1 / 3;
        grid-column: 1 / -1;

        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;

        div {
            font-size: 34px;
            color: var(--md-sys-color-primary);
        }
    }

}

.pagination {
    display: flex;
    gap: 12px;
    margin: 12px 0;
    justify-content: space-evenly;
}